<template>
  <div class="info">
    <h1>工作/Work</h1>
    <transition :duration="{ enter: 2500, leave: 1500 }" enter-active-class="animated fadeInRight">
      <div class="cont" v-if="show">
        <el-row :gutter="20">
          <template v-for="item in profiles">
            <el-col :key="item.name" :span="24" :xs="24">
              <div class="item">
                <h2 v-text="item.name"></h2>
                <p v-text="'技术栈: ' + item.skills"></p>
                <p v-text="item.content"></p>
                <!-- <p v-text="'github地址: ' + item.github"></p> -->
              </div>
            </el-col>
          </template>
        </el-row>
      </div>
    </transition>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false,
      profiles: [
        // {
        //   name: "VuePress技术文档(learn)",
        //   skills: "vue",
        //   content: "利用Vue+Github写成一个技术文档",
        //   github: ""
        // },
        // {
        //   name: "个人博客后台页面",
        //   skills: "vue vue-cli element-ui mysql",
        //   content:
        //     "利用Vue+element-ui快速搭建与数据库(Mysql)链接的后台操作页面",
        //   github: ""
        // },
        {
          name:"我",
          skills:"Vue element-ui es6",
          content:"利用Vue+element-ui构建网页框架，且调用后端api接口进行网页数据渲染交互，完成任务。下班时间喜欢尝试新的技术，并且放在自己的个人Vue项目中。"
        }
      ]
    };
  },
  methods: {
    timeout() {
      setTimeout(() => {
        this.show = true;
      }, 1000);
    }
  },
  mounted() {
    this.timeout();
  }
};
</script>

<style lang="less" scoped>
@import "../less/index.less";
.info {
  .info;
  .cont {
    .cont;
    width: 90%;
    .item {
      background-color: rgba(59, 123, 13, 0.2);
      padding: 10px;
      border-radius: 15px;
      margin: 10px auto;
      @media screen and (max-width: 768px) {
        padding: 5px;
        margin: 5px auto;
      }
      &:hover {
        border-radius: 25px;
        background-color: rgba(10, 135, 87, 0.3);
      }
      p {
        text-align: left;
        text-indent: 28px;
        font-size: 1.1rem;
        line-height: 1.2;
        @media screen and (max-width: 768px) {
          text-indent: 12px;
          font-size: 0.7rem;
          line-height: 1.1;
        }
      }
    }
  }
}
</style>



